<template>
  <ActionList title="链接收徒">
    <Action @click.native="showLinkPopup()">
      <img slot="icon" src="//qianka.b0.upaiyun.com/images/63459a3d3cd307311775dacbf696c9ca.png">
      <span slot="title">链接收徒</span>
      <span slot="desc">最经典最万能 ‧ 哪里都能发</span>
    </Action>
  </ActionList>
</template>

<script>
  import {Action, ActionList} from './actions'
  import {popup_apprentice_link} from '../popups.js'

  export default {
    props: {
      shoutuUrl: Function
    },

    components: {
      Action,
      ActionList
    },

    methods: {
      showLinkPopup () {
        let popup = $popup.fromTemplate(
          popup_apprentice_link.template(this.shoutuUrl()),
          popup_apprentice_link.options
        )
        popup.show()
      }
    }
  }
</script>



