<!-- 分享收徒 Actions -->
<template>
  <ActionList title="分享收徒">
    <!-- 分享到微信、朋友圈、QQ、QQ空间 -->
    <Action v-for="channel in channels" :key="channel.type" @click.native="onShare(channel.type)">
      <img slot="icon" :src="channel.icon">
      <span slot="title" v-text="channel.title"></span>
      <span slot="desc" v-text="channel.desc"></span>
    </Action>
  </ActionList>
</template>

<script>
  /* eslint eqeqeq: "off", camelcase: "off" */
  import {Action, ActionList} from './actions'

  import { popup_open_assistant_lite, popup_install_or_launch_assistant } from 'services/assistant/popups.js'

  // 分享类型
  import {
    SHARE_WX,
    SHARE_QQ,
    SHARE_QZONE,
    SHARE_WXTIMELINE,
    SHARE_WEIBO
  } from './constants'

  const SHARE_CHANNELS = [
    {
      type: SHARE_WX,
      icon: '//qianka.b0.upaiyun.com/images/605c8b528c36a0143981dd2421be3b5d.png',
      title: '微信收徒',
      desc: '可单发可群发 ‧ 针对性强'
    },
    {
      type: SHARE_WXTIMELINE,
      icon: '//qianka.b0.upaiyun.com/images/9bd0706667d9bed7d5d797e858778360.png',
      title: '朋友圈收徒',
      desc: '静静的广而告之 ‧ 覆盖面广'
    },
    {
      type: SHARE_QQ,
      icon: '//qianka.b0.upaiyun.com/images/cd7c03a9812479a10cc06d4cce4fbbe5.png',
      title: 'QQ收徒',
      desc: '可单发可群发 ‧ 针对性强'
    },
    {
      type: SHARE_QZONE,
      icon: '//qianka.b0.upaiyun.com/images/cb2f24a2d5deb4900af99aea7fd664b5.png',
      title: '空间收徒',
      desc: '静静的广而告之 ‧ 覆盖面广'
    },
    {
      type: SHARE_WEIBO,
      icon: '//qianka.b0.upaiyun.com/images/17df577cb74a39640dfd0e277a4cfabb.png',
      title: '微博收徒',
      desc: '可单发可群发 ‧ 针对性强'
    }
  ]

  export default {
    props: {
      shareUrls: Function,
      assistantInfo: Function,
      isNewbie: Number
    },

    components: {
      Action,
      ActionList
    },

    data () {
      return {
        channels: SHARE_CHANNELS
      }
    },

    methods: {
      onShare (type) {
        let { app_icon, download_url, app_name, scheme_url } = this.assistantInfo()

        if (this.isNewbie == 1) {
          // 新人下载、激活弹层
          this.showDownloadAssistantPopup(app_icon, app_name, download_url, scheme_url, type)
        } else {
          // 老用户激活弹层 （即分享）
          this.showOpenAssistantPopup(download_url, type)
        }
      },

      // 激活助手弹层
      showOpenAssistantPopup (downloadUrl, type) {
        var self = this
        let popup = $popup.fromTemplate(
          popup_open_assistant_lite.template(downloadUrl, 'apprentice'),
          {
            effect: 'scale',
            cssClass: 'popup-open-assistant-lite',
            showClose: true,
            closeCallback: function () {},
            keep: true,
            buttons: [
              {
                text: '立即激活',
                theme: 'assertive',
                onClick: (buttonIndex) => {
                  // 去分享
                  self._doShare(type)
                  popup.hide()
                }
              }
            ]
          }
        )
        popup.show()
      },

      // 下载安装助手或直接打开助手 新人下载、打开助手弹层
      showDownloadAssistantPopup (app_icon, app_name, download_url, scheme_url, type) {
        var self = this
        let popup = $popup.fromTemplate(
          popup_install_or_launch_assistant.template(app_icon, app_name),
          {
            effect: 'scale',
            cssClass: 'popup-download-assistant lite',
            showClose: true,
            closeCallback: function () {
              popup.hide()
            },
            buttons: [
              {
                text: '立即下载',
                theme: 'assertive',
                onClick: () => {
                  location.href = download_url
                }
              },
              { text: '激活助手',
                onClick: function () {
                  // 去分享
                  self._doShare(type)
                  popup.hide()
                }
              }
            ],
            keep: true
          }
        )

        popup.show()
      },

      _doShare (type) {
        if (type == SHARE_WX) { // 微信好友
          location.href = this.shareUrls()['wx']
        }

        if (type == SHARE_QQ) { // QQ好友
          location.href = this.shareUrls()['qq']
        }

        if (type == SHARE_WXTIMELINE) { // 微信朋友圈
          location.href = this.shareUrls()['pyq']
        }

        if (type == SHARE_QZONE) { // QQ空间
          location.href = this.shareUrls()['qzon']
        }

        if (type == SHARE_WEIBO) { // 微博
          location.href = this.shareUrls()['weibo']
        }
      }
    }
  }
</script>



