<!-- 分享收徒 Actions -->
<template>
  <ActionList title="分享收徒">
    <!-- 分享到微信、朋友圈、QQ、QQ空间 -->
    <Action v-for="channel in channels" :key="channel.type" @click.native="onShare(channel.type)">
      <img slot="icon" :src="channel.icon">
      <span slot="title" v-text="channel.title"></span>
      <span slot="desc" v-text="channel.desc"></span>
    </Action>
  </ActionList>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex'
  import {
    KEY_UID
  } from 'constants'
  import {Action, ActionList} from './actions'

  import {
    popup_install_app
  } from '../popups.js'

  import downloadAssistantPopupMixin from '@/task/views/downloadAssistantPopupMixin'

  const makeShareParams = (url, shareType, income) => {
    let uid = $storage.get(KEY_UID)
    return {
      url: url,
      title: `我试玩APP已经赚了${parseFloat(income)}元，爽呆...`,
      desc: '　',
      img: 'http://qianka.b0.upaiyun.com/images/dcba469b9aabb2dfdb18a03036a91b39.png',
      type: shareType,
      jump_url: `${__ENV__.ws_endpoint}/v4/key/again_share?user_id=${uid}&income=${income}&url=${encodeURIComponent(url)}`
    }
  }

  // BUNDLE ID
  const BID_WX = 'com.tencent.xin'
  const BID_QQ = 'com.tencent.mqq'

  const getBundleId = (shareType) => {
    switch (shareType) {
      case SHARE_WX:
        return BID_WX
      case SHARE_QQ:
        return BID_QQ
      case SHARE_QZONE:
        return BID_QQ
      case SHARE_WXTIMELINE:
        return BID_WX
    }
  }

  // 分享类型
  import {
    SHARE_WX,
    SHARE_QQ,
    SHARE_QZONE,
    SHARE_WXTIMELINE
  } from './constants'

  const is_ios_11 = () => {
    let re = /OS ((\d{1,2})_\d{1,2}||(\d{1,2})_\d{1,2}_\d{1,2}) like Mac OS X/i

    let result = re.exec(window.navigator.userAgent)
    let ios_ver = result && result[1] && result[1].substr(0, 2)
    return parseInt(ios_ver) === 11
  }

  const WEIXIN = {
    name: '微信',
    url: 'https://itunes.apple.com/cn/app/%E5%BE%AE%E4%BF%A1/id414478124?mt=8'
  }
  const QQ = {
    name: 'QQ',
    url: 'https://itunes.apple.com/cn/app/qq/id444934666?mt=8'
  }

  // 请安装微信或QQ的提示弹层
  const shareApp = (shareType) => {
    switch (shareType) {
      case SHARE_WX:
        return WEIXIN
      case SHARE_QQ:
        return QQ
      case SHARE_QZONE:
        return QQ
      case SHARE_WXTIMELINE:
        return WEIXIN
    }
  }

  const showInstallAppPopup = (shareType) => {
    let app = shareApp(shareType)

    let p = popup_install_app
    let popup = $popup.fromTemplate(
      p.template(app.name),
      p.options
    )

    popup.show().then((buttonIndex) => {
      if (buttonIndex === 1) {
        window.location.href = app.url
      }
    })
  }

  const SHARE_CHANNELS = [
    {
      type: SHARE_WX,
      icon: '//qianka.b0.upaiyun.com/images/605c8b528c36a0143981dd2421be3b5d.png',
      title: '微信收徒',
      desc: '可单发可群发 ‧ 针对性强'
    },
    {
      type: SHARE_WXTIMELINE,
      icon: '//qianka.b0.upaiyun.com/images/9bd0706667d9bed7d5d797e858778360.png',
      title: '朋友圈收徒',
      desc: '静静的广而告之 ‧ 覆盖面广'
    },
    {
      type: SHARE_QQ,
      icon: '//qianka.b0.upaiyun.com/images/cd7c03a9812479a10cc06d4cce4fbbe5.png',
      title: 'QQ收徒',
      desc: '可单发可群发 ‧ 针对性强'
    },
    {
      type: SHARE_QZONE,
      icon: '//qianka.b0.upaiyun.com/images/cb2f24a2d5deb4900af99aea7fd664b5.png',
      title: '空间收徒',
      desc: '静静的广而告之 ‧ 覆盖面广'
    }
  ]

  export default {
    props: {
      shareData: Function
    },

    components: {
      Action,
      ActionList
    },

    mixins: [downloadAssistantPopupMixin],

    data () {
      return {
        channels: SHARE_CHANNELS,
        assistantGuideModal: undefined,
        is_ios_11: is_ios_11()
      }
    },

    computed: mapGetters('assistant', [
      'isNewbie'
    ]),

    methods: {
      ...mapActions('assistant', {
        'isAssistantAlive': 'alive',
        'syncAssistantUnopen': 'syncAssistantUnopen',
        'syncAssistantInvalid': 'syncAssistantInvalid'
      }),

      onShare (type) {
        if (!is_ios_11()) {
          this._onShare(type)
        } else {
          this._onShareWith11(type)
        }
      },

      _onShare (type) {
        this.isAssistantAlive() // 判断助手是否开启
          .then(() => {
            // 判断是否安装微信或QQ
            return $assistant.getAppInfo(getBundleId(type))
          }, () => {
            // 助手未开启，则展示打开助手（或下载助手）弹层

            if (this.isNewbie) { // 如果是新用户的话
              this.showDownloadAssistantPopup()
              return
            }

            this.syncAssistantUnopen()
            this.syncAssistantInvalid()
          })
          .then(res => {
            let payload = res.data && res.data.payload
            return payload && payload.bundle_id
          })
          .then(result => {
            if (result) {
              let shareData = this.shareData(type)
              let params = makeShareParams(shareData.url, type, shareData.income)
              $assistant.share(params)
            } else {
              showInstallAppPopup(type)
            }
          })
      },

      _onShareWith11 (type) {
        this.isAssistantAlive() // 判断助手是否开启
          .then(() => {
            let shareData = this.shareData(type)
            let params = makeShareParams(shareData.url, type, shareData.income)
            $assistant.share(params)
          }, () => {
            // 助手未开启，则展示打开助手（或下载助手）弹层

            if (this.isNewbie) { // 如果是新用户的话
              this.showDownloadAssistantPopup()
              return
            }

            this.syncAssistantUnopen()
            this.syncAssistantInvalid()
          })
      }
    }
  }
</script>



