<!-- 收徒任务首页 -->
<template>
  <div class="page apprentice-page" v-nav="{hideNavbar: true}">
    <!-- 页头 -->
    <AnimateHeader
      bg-color="#44B549"
      menu-bg-color="#3DA742"
      color="#FFFFFF"
      title="收徒赚钱"
      sub-title="① 邀请好友 &nbsp;② 好友做任务 &nbsp;③ 我拿奖励"
      :on-back-button-click="back"
      :expanded="true"
    >
    </AnimateHeader>

    <div class="page-content has-animate-header bg-light">
      <!-- 新老用户规则 -->
      <Rule :data="ruleData"></Rule>

      <!-- 分享收徒 Actions -->
      <ShareActions v-if="shareActionsType == 1" :share-data="getShareData" />
      <ShareActionsLite v-else-if="shareActionsType == 2" :is-newbie="is_newbie" :share-urls="getShareUrls" :assistant-info="getAssistantInfo" />

      <!-- 扫码收徒 Actions -->
      <QrcodeActions :qrcode-data="getQrcodeData" />

      <!-- 链接收徒 Actions -->
      <LinkActions :shoutu-url="getShoutuUrl"/>
    </div>

    <AssistantInvalid mode="manual" />
    <AssistantUnopen mode="manual" />

    <div style="font-family: hzgb;">0.123456789</div>

  </div>
</template>

<style lang="scss">
  .apprentice-page .bg-light {
    padding-top: 100px;
    background: #FFF;
  }
</style>

<script>
  /* eslint eqeqeq: "off", camelcase: "off" */
  import { KEY_SHOUTU_TYPE } from 'constants'
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  import Rule from './Rule.vue'
  import ShareActions from './ShareActions.vue'
  import ShareActionsLite from './ShareActionsLite.vue'
  import QrcodeActions from './QrcodeActions.vue'
  import LinkActions from './LinkActions.vue'

  import AssistantInvalid from '@/task/popups/AssistantInvalid.vue'
  import AssistantUnopen from '@/task/popups/AssistantUnopen.vue'

  export default {
    components: {
      AnimateHeader,
      Rule,
      ShareActions,
      ShareActionsLite,
      QrcodeActions,
      LinkActions,

      AssistantInvalid,
      AssistantUnopen
    },

    mounted () {
      this.fetchData()
    },

    data () {
      return {
        pageTitle: '收徒任务',
        ruleData: {
          apprentice_num: '',
          grandson_num: '',
          income: '',
          shoutu_type: '',
          today_apprentice_num: '',
          today_grandson_num: '',
          today_income: ''
        },

        shareData: {
          url: '',
          income: ''
        },

        qrcodeData: {
          uid: 0,
          qrcode_image: '',
          total_income: '',
          apprentice_num: 0
        },

        shoutuUrl: '',

        allShoutuUrl: {
          share_shoutu_url_qq: '',
          share_shoutu_url_qzon: '',
          share_shoutu_url_wx: '',
          share_shoutu_url_pyq: ''
        },

        url_qq: '',

        shareActionsType: 0, // 1: 线上钥匙、企业钥匙 2: lite 版
        liteShareUrls: {},
        liteAssistantInfo: {},
        is_newbie: 0
      }
    },

    methods: {
      fetchData () {
        $api
          .shoutuDashboard()
          .then(res => res.data && res.data.payload)
          .then(payload => {
            this.shareActionsType = payload.lite ? 2 : 1

            _.extend(this.ruleData, {
              apprentice_num: payload.apprentice_num,
              grandson_num: payload.grandson_num,
              income: payload.shoutu_income,
              shoutu_type: payload.shoutu_type,
              today_apprentice_num: payload.today_apprentice_num,
              today_grandson_num: payload.today_grandson_num,
              today_income: payload.today_income
            })

            _.extend(this.shareData, {
              url: payload.share_shoutu_url,
              income: payload.total_income
            })

            _.extend(this.allShoutuUrl, {
              share_shoutu_url_qq: payload.share_shoutu_url_qq,
              share_shoutu_url_qzon: payload.share_shoutu_url_qzon,
              share_shoutu_url_wx: payload.share_shoutu_url_wx,
              share_shoutu_url_pyq: payload.share_shoutu_url_pyq
            })

            $storage.set(KEY_SHOUTU_TYPE, payload.shoutu_type) // shoutu_type 1为老用户规则  2为新用户规则

            this.shoutuUrl = payload.shoutu_url

            this.url_qq = payload.share_shoutu_url_qq

            _.extend(this.qrcodeData, {
              uid: payload.user_id,
              qrcode_image: payload.qrcode_url,
              total_income: payload.total_income,
              apprentice_num: payload.apprentice_num,
              cjd_qrcode_image: payload.cjd_qrcode_url
            })

            // lite 版扩展字段处理
            if (payload.lite) {
              // lite 版 share urls
              this.liteShareUrls = payload.lite.share_urls
              this.is_newbie = parseFloat(payload.lite.is_newbie)

              // lite 版 assitantInfo
              this.liteAssistantInfo = payload.lite.assistant_info
            }
          })
      },

      getShareData (type) {
        switch (type) {
          case 1:
            this.shareData.url = this.allShoutuUrl.share_shoutu_url_wx
            break
          case 2:
            this.shareData.url = this.allShoutuUrl.share_shoutu_url_qq
            break
          case 3:
            this.shareData.url = this.allShoutuUrl.share_shoutu_url_qzon
            break
          case 4:
            this.shareData.url = this.allShoutuUrl.share_shoutu_url_pyq
            break
        }
        return this.shareData
      },

      getShareUrls () {
        return this.liteShareUrls
      },

      getAssistantInfo () {
        return this.liteAssistantInfo
      },

      getQrcodeData () {
        return this.qrcodeData
      },

      getShoutuUrl () {
        return this.shoutuUrl
      },

      back () {
        let fromShoutuPortal = this.$route.query.from_shoutu_portal
        if (fromShoutuPortal == 1) {
          $router.back('/tasks')
          return
        } else if (fromShoutuPortal == 2) {
          $router.back('/tasks/lite')
          return
        }

        let path = _.get(this.$route.query, 'backroute', '/dashboard')
        $router.back({ path })
      }
    }
  }
</script>



