<!-- 二维码、成绩单收徒 -->
<template>
  <ActionList title="扫码收徒">
    <Action v-for="channel in channels" :key="channel.type"
      @click.native="showPopup(channel.type)"
    >
      <img slot="icon" :src="channel.icon">
      <span slot="title" v-text="channel.title"></span>
      <span slot="desc" v-text="channel.desc"></span>
    </Action>
  </ActionList>
</template>

<script>
  import {Action, ActionList} from './actions'

  import {
    popup_apprentice_qrcode,
    popup_apprentice_card
  } from '../popups.js'

  const CHANNELS = [
    {
      type: 'qrcode', // 收徒二维码
      icon: '//qianka.b0.upaiyun.com/images/2c2466f59b04f8e516f2da231a4333f7.png',
      title: '二维码收徒',
      desc: '特别适合当面收徒 ‧ 简单明了'
    },
    {
      type: 'card', // 成绩单
      icon: '//qianka.b0.upaiyun.com/images/877134208b1ce68d8e960e2ac0fe3ba9.png',
      title: '成绩单收徒',
      desc: '钱咖成就一目了然 ‧ 用数据说话'
    }
  ]

  export default {
    components: {
      Action, ActionList
    },

    props: {
      qrcodeData: Function
    },

    data () {
      return {
        channels: CHANNELS
      }
    },

    methods: {
      showPopup (type) {
        switch (type) {
          case 'qrcode':
            this._showQrcodePopup()
            break
          case 'card':
            this._showCardPopup()
            break
        }
      },

      // 二维码
      _showQrcodePopup () {
        let {uid, qrcode_image} = this.qrcodeData()
        let popup = $popup.fromTemplate(
          popup_apprentice_qrcode.template(uid, qrcode_image),
          popup_apprentice_qrcode.options
        )
        popup.show()

        let c = document.getElementById('qrcode-content')
        let ctx = c.getContext('2d')
        let bgUrl = c.getAttribute('data-bg')
        let bg = new Image()
        bg.src = bgUrl
        bg.onload = () => {
          // 二维码
          ctx.drawImage(bg, 116, 80, 320, 320)

          // 文案
          ctx.font = '24px PingFangSC-Regular'

          ctx.fillStyle = '#3a3a3a'
          ctx.textAlign = 'center'
          ctx.fillText('扫码加入 每月多赚        元', 275, 428 + 24, 550, 24)

          ctx.fillStyle = 'red'
          ctx.textAlign = 'center'
          ctx.fillText('                      500', 275, 428 + 24, 550, 24)

          ctx.fillStyle = '#3a3a3a'
          ctx.textAlign = 'center'
          ctx.fillText(`安卓用户请填写我的ID：${uid}`, 275, 428 + 24 + 44, 550, 24)

          let t = document.getElementById('qrcode-copy-content')
          let img = document.createElement('img')
          img.src = c.toDataURL()
          t.appendChild(img)
        }
      },

      // 成绩单
      _showCardPopup () {
        let {uid, total_income, apprentice_num, cjd_qrcode_image} = this.qrcodeData() // eslint-disable-line
        let popup = $popup.fromTemplate(
          popup_apprentice_card.template(),
          popup_apprentice_card.options
        )
        popup.show()

        let c = document.getElementById('card-content')
        let ctx = c.getContext('2d')
        let bg = new Image()
        bg.src = '/diablo/dcaabbda-d8f9-4ccd-bdff-70fcae94ca7c.png'
        bg.onload = () => {
          // 背景图
          ctx.drawImage(bg, 0, 0, 550, 726)

          // 文案：累计收入(元)
          ctx.font = '22px PingFangSC-Regular'

          ctx.fillStyle = '#ffffff'
          ctx.textAlign = 'center'
          ctx.fillText('累计收入(元)', 275, 40 + 16, 550, 16)

          // 文案：累计收入金额
          ctx.font = '60px hzgb'
          ctx.fillText(total_income, 275, 92 + 30, 550, 30)

          // 文案：收徒人数
          //  1. 外框
          let x = 162
          let y = 172
          let w = 226
          let h = 42
          let r = h / 2
          ctx.beginPath()
          ctx.fillStyle = 'rgba(134,107,173,0.40)'
          ctx.rect(x + r, y, w - h, h)
          ctx.arc(x + r, y + r, r, 0.5 * Math.PI, 1.5 * Math.PI, false)
          ctx.arc(x + w - r, y + r, r, 1.5 * Math.PI, 0.5 * Math.PI, false)
          ctx.fill()
          //  2. 文字
          ctx.font = '24px PingFangSC-Regular'
          ctx.fillStyle = '#ffffff'
          ctx.fillText(`共收徒 ${apprentice_num} 人`, 275, y + 24 + 5, w, h)

          // 二维码外框
          x = 148
          y = 328
          w = 254
          h = 254
          r = 8

          ctx.fillRect(x + r, y, w - 2 * r, h)
          ctx.fillRect(x, y + r, w, h - 2 * r)

          ctx.beginPath()
          ctx.arc(x + r, y + r, r, 0, 2 * Math.PI, false)
          ctx.fill()

          ctx.beginPath()
          ctx.arc(x + r, y + h - r, r, 0, 2 * Math.PI, false)
          ctx.fill()

          ctx.beginPath()
          ctx.arc(x + w - r, y + r, r, 0, 2 * Math.PI, false)
          ctx.fill()

          ctx.beginPath()
          ctx.arc(x + w - r, y + h - r, r, 0, 2 * Math.PI, false)
          ctx.fill()

          // 二维码
          let qrcode = new Image()
          qrcode.src = cjd_qrcode_image
          qrcode.onload = () => {
            ctx.drawImage(qrcode, 148 + 20, 328 + 20, 254 - 40, 254 - 40)

            // 底部文案
            ctx.fillText('扫码加入 每月多赚 500 元', 275, 618 + 24, 550, 24)
            ctx.fillText(`安卓用户请填写我的ID：${uid}`, 275, 618 + 24 + 44, 550, 24)

            let t = document.getElementById('card-copy-content')
            let img = document.createElement('img')
            img.src = c.toDataURL()
            t.appendChild(img)
          }
        }
      }
    }
  }
</script>



