<!-- 新、老规则内容详情 -->
<template>
  <div class="content">
    <div class="user-info-title">
      <div v-if="data.shoutu_type == 1">
        <div class="user-info">
          <div class="info-wrapper first-wrapper" @click="achievements(0)">
            <div class="title">奖励</div>
            <div class="num" v-text="data.income"></div>
          </div>
          <div class="info-wrapper" @click="achievements(1)">
            <div class="title">徒弟</div>
            <div class="num" v-text="data.apprentice_num"></div>
          </div>
        </div>
      </div>

      <div v-if="data.shoutu_type == 2">
        <div class="user-info new-rule">
          <div class="info-wrapper" @click="achievements(0)">
            <div class="title">奖励</div>
            <div class="num" v-text="data.income"></div>
          </div>
          <div class="info-wrapper" @click="achievements(1)">
            <div class="title">徒弟</div>
            <div class="num" v-text="data.apprentice_num"></div>
          </div>
          <div class="info-wrapper" @click="achievements(2)">
            <div class="title">徒孙</div>
            <div class="num" v-text="data.grandson_num"></div>
          </div>
        </div>
      </div>
    </div>
    <div class="user-info-md">
      <div class="info-md" v-if="data.shoutu_type == 2">
        <div class="md-line"><div class="square"></div><span>两层奖励，上不封顶</span></div>
        <div class="md-line"><div class="square"></div><span><span class="bold">徒弟 </span>完成任务，我拿 <span class="light">10%</span> 提成奖励</span></div>
        <div class="md-line"><div class="square"></div><span><span class="bold">徒孙(徒弟的徒弟) </span>完成任务，我还拿 <span class="light">10%</span> 提成奖励</span></div>
        <div class="rule-img"><img src="//assets.qkcdn.com/images/a32fc40f323f59195b101d4d9fb3dcc4.png"></div>
        <div class="question new" @click="rulePopup('new')"></div>
      </div>
      <div class="info-md" v-if="data.shoutu_type == 1">
        <div class="md-line"><div class="square"></div><span>每个徒弟最高10元</span></div>
        <div class="md-line"><div class="square"></div><span>徒弟完成iOS任务，我拿 <span class="light">100%</span> 等额奖励</span></div>
        <div class="md-line"><div class="square"></div><span>徒弟完成安卓任务，我拿 <span class="light">20%</span> 提成奖励</span></div>
        <div class="rule-img"><img src="//assets.qkcdn.com/images/10394a8287a23a27821e1efcffa6603b.png"></div>
        <div class="question" @click="rulePopup('old')"></div>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  @import '../../../scss/mixins';

  .content {
    padding: 0 16px;

    .user-info-title {
      margin-top: 20px;
      margin-bottom: 25px;

      .user-info {
        width: 100%;
        height: 80px;

        .info-wrapper {
          width: 166px;
          height: 80px;
          border-radius: 4px;
          background-color: #f5f5f5;
          display: inline-block;

          .title {
            font-size: 12px;
            font-weight: 600;
            color: #3a3a3a;
            text-align: center;
            margin-top: 15px;

            @include font-semibold();
          }

          .num {
            text-align: center;
            font-family: GiorgioSans-Bold;
            font-size: 32px;
            height: 32px;
            line-height: 32px;
            font-weight: bold;
            color: #ef502b;
          }

          &.first-wrapper {
            margin-right: 6px;
          }
        }

        &.new-rule {
          .info-wrapper {
            width: 111px;
          }
        }
      }
    }

    .user-info-md {
      margin-bottom: 25px;

      .info-md {
        .md-line {
          color: #484746;
          height: 17px;
          line-height: 17px;
          margin-bottom: 5px;
          font-size: 0px;

          @include font-regular();

          &:nth-child(3) {
            margin-bottom: 18px;
          }

          .square {
            width: 4px;
            height: 4px;
            background-color: #dddddd;
            display: inline-block;
            margin-right: 10px;
            vertical-align: middle;
          }

          span {
            display: inline-block;
            vertical-align: top;
            font-size: 12px;
          }

          .bold {
            font-weight: 600;
          }

          .light {
            font-weight: 600;
            color: #ef4f2a;
          }
        }

        .rule-img {
          img {
            width: 343px;
            height: 134px;
          }
        }

        .question {
          width: 12px;
          height: 12px;
          position: absolute;
          left: 224px;
          top: 210px;

          &.new {
            left: 215px;
          }
        }
      }
    }
  }
</style>
<script>
  import {popup_rule_new, popup_rule_old} from '../popups'
  export default {
    props: {
      data: Object
    },
    data () {
      return {
        oldUserTodayIncome: true, // 老用户今日奖励状态
        newUserTodayIncome: true // 新用户今日奖励状态
      }
    },
    mounted () {
      this.fetchData()
    },
    methods: {
      fetchData: function () {
        setTimeout(() => {
          if (this.data.shoutu_type === 1) { // shoutu_type 1为老用户规则  2为新用户规则
            parseFloat(this.data.today_income) === 0 && this.data.today_apprentice_num === 0
            ? this.oldUserTodayIncome = true
            : this.oldUserTodayIncome = false
          } else {
            parseFloat(this.data.today_income) === 0 && this.data.today_apprentice_num === 0 && this.data.today_grandson_num === 0
            ? this.newUserTodayIncome = true
            : this.newUserTodayIncome = false
          }
        }, 500)
      },
      achievements: function (index) {
        let route
        switch (index) {
          case 0:
            route = { path: '/apprentice/achievements/income' }
            break
          case 1:
            route = { path: '/apprentice/achievements/list/1' }
            break
          case 2:
            route = { path: '/apprentice/achievements/list/2' }
            break
        }
        $router.forward(route)
      },
      rulePopup (type) {
        let popup
        if (type === 'new') {
          popup = $popup.fromTemplate(
            popup_rule_new.template,
            popup_rule_new.options
          )
        } else {
          popup = $popup.fromTemplate(
            popup_rule_old.template,
            popup_rule_old.options
          )
        }

        popup.show()
      }
    }
  }
</script>



